#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  font-family: Avenir, Helvetica, Arial, sans-serif;
}

html, body {
  overscroll-behavior-y: none;
  overscroll-behavior-x: none;
  scroll-behavior: smooth;
  height: 100%;
  background-color: #000;
  margin: 0;
  font-family: Avenir, Helvetica, Arial, sans-serif;
}

#bg {
  position: fixed;
  top: 0;
  left: 0;
}

#business-card {
  width: 100%;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  background: url("background.33bee0a4.png") 0 0 / cover fixed;
  grid-template-rows: 20% 200px auto;
  grid-template-columns: 10% 400px auto 40% 10%;
  display: grid;
  position: relative;
  overflow: hidden;
}

@media screen and (max-width: 800px) {
  #business-card {
    scroll-snap-align: end;
    grid-template-rows: 200px auto;
    grid-template-columns: 100%;
  }
}

#business-card #card {
  color: #222;
  background-color: #eee;
  outline: 8px solid #222;
  grid-area: 2 / 2 / 3 / 3;
}

@media screen and (max-width: 800px) {
  #business-card #card {
    grid-area: 1 / 1 / 2 / 2;
  }
}

#business-card #card h1 {
  margin-bottom: 0;
  margin-left: 24px;
  font-size: 36px;
  font-weight: 300;
}

#business-card #card subtitle {
  color: #333;
  margin-left: 24px;
  font-weight: 100;
}

#business-card #card p {
  color: #333;
  margin-left: 24px;
  font-weight: 300;
}

#business-card #card p a {
  color: #555;
}

#business-card #earth-wrap {
  flex-direction: column;
  grid-area: 1 / 3 / 4 / 6;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 800px) {
  #business-card #earth-wrap {
    cursor: grab;
    grid-area: 2 / 1 / 3 / 2;
  }
}

#business-card #earth-wrap #earth {
  width: 100%;
  max-width: 95vh;
  aspect-ratio: 1 / 1;
}

@media screen and (max-width: 800px) {
  #business-card #earth-wrap #earth {
    max-width: 85vh;
  }
}

#business-card .swipedown {
  width: 10%;
  position: absolute;
  bottom: 0;
}

#business-card .swipedown p {
  -webkit-text-stroke: 2px black solid;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 24px;
  font-weight: bold;
}

#business-card .contacts {
  flex-direction: row;
  justify-content: flex-end;
  gap: 16px;
  padding: 15px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media screen and (max-width: 800px) {
  #business-card .contacts {
    justify-content: center;
  }
}

#business-card .contacts a {
  color: #eee;
  letter-spacing: 2px;
  background-color: #fff0;
  outline: 2px solid #eee;
  padding: 8px 12px;
  font-family: monospace;
  font-size: 22px;
  font-weight: 300;
  text-decoration: none;
  transition: outline .2s, background-color .2s;
}

#business-card .contacts a:hover {
  background-color: #fff3;
  outline: 5px solid #fff;
}

#portfolio {
  min-height: 100%;
  width: 100%;
  background-color: #eee;
  grid-template: "title projects" 1fr
  / auto 1fr;
  display: grid;
}

@media screen and (max-width: 800px) {
  #portfolio {
    grid-template: "title"
                   "projects" 1fr
                   / 1fr;
  }
}

#portfolio .title {
  grid-area: title;
}

#portfolio .title h1 {
  color: #333;
  margin: 20px 0 0 20px;
  font-size: 48px;
  position: sticky;
  top: 20px;
}

@media screen and (max-width: 800px) {
  #portfolio .title h1 {
    margin: 20px 0 -20px 60px;
  }
}

#portfolio .projects {
  flex-direction: column;
  grid-area: projects;
  gap: 20px;
  padding: 40px 40px 80px;
  display: flex;
}

#portfolio .projects .project {
  max-width: 700px;
  border: 1px solid #aaa;
  border-radius: 20px;
  grid-template: "text text" 1fr
                 ". buttons"
                 / 1fr auto;
  padding: 20px 10px 10px 20px;
  transition: box-shadow .2s;
  display: grid;
  box-shadow: 0 0 0 2px #aaaaa033;
}

#portfolio .projects .project:hover {
  box-shadow: 0 0 0 5px #aaaaa033;
}

#portfolio .projects .project .text {
  grid-area: text;
}

#portfolio .projects .project .text h2 {
  color: #444;
  margin: 0;
  font-size: 32px;
}

#portfolio .projects .project .text .description {
  color: #444;
  margin: 0;
}

#portfolio .projects .project .text .tools {
  width: 100%;
  gap: 8px;
  margin-top: 8px;
  display: flex;
  overflow-x: auto;
}

#portfolio .projects .project .text .tools span {
  color: #a5a5a5;
}

#portfolio .projects .project .buttons {
  grid-area: buttons;
  align-items: center;
  gap: 8px;
  display: flex;
}

#portfolio .projects .project .buttons a.try {
  color: #444;
  letter-spacing: 2px;
  background-color: #2c2c2c00;
  padding: 8px 12px;
  font-family: monospace;
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;
  transition: outline .2s, background-color .2s;
}

#portfolio .projects .project .buttons a.try:hover {
  color: #222;
}

#portfolio .projects .project .buttons a.github {
  height: 40px;
  width: 40px;
  display: block;
}

#portfolio .projects .project .buttons a.github img {
  height: 100%;
  opacity: .8;
}

#portfolio .projects .project .buttons a.github img:hover {
  opacity: 1;
}

